.tb_mc_post_container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_mc_post_wrapper {
    width: 25%;
    padding: 7.5px;
}

.tb_mc_post_wrapper_width_2 {
    width: 50%;
    padding: 7.5px;
}

.tb_mc_post_wrap_in {
    border: 1px solid rgba(0, 0, 0, 0.1);
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}

.tb_mc_post_in {
    display: block;
    position: relative;
    cursor: pointer;
}

.tb_mc_image_wrap_ {
    position: relative;
    padding-bottom: 100%;
}

.tb_mc_image, .tb_mc_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}

.tb_mc_video {
    background-color: #000;
}

.tb_mc_video video,
.tb_mc_video iframe {
    object-fit: cover;
    object-position: center;
}

.tb_mc_video>div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_mc_image_wrap_ img {
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0 !important;
}

.te_mc_post_wrap_in .tb_mc_share_container {
    left: 18px;
    top: 80px;
}

.tb_mc_contant_wrapper,
.tb_mc_author_container {
    padding: 10px;
}

.tb_mc_author {
    width: calc(100% - 30px);
}

.tb_mc_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    overflow: hidden;
}

.tb_mc_author_profile__ {
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_mc_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    text-align: left;
    width: 100%;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_mc_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_mc_username,
.tb_mc_time {
    font-family: var(--tb-font);
    color: var(--tb-author-color);
}

.tb_mc_seprator {
    background-color: var(--tb-author-color);
}

.tb_mc_content,
.tb_mc_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_mc_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_mc_author_wrapper {
    display: flex;
    align-items: center;
}

.tb_mc_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
    align-items: flex-start;
}

.tb_mc_post_info {
    display: flex;
    align-items: center;
    width: 100%;
}

.tb_mc_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_mc_username,
.tb_mc_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    line-height: 1.5;
    overflow: hidden;
    line-height: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.tb_mc_username {
    max-width: 65%;
}

.tb_mc_time {
    max-width: 35%;
}

.tb_mc_social_ {
    font-size: 24px;
    margin: 5px 0;
}

.tb_mc_social_center_ico {
    font-size: 24px;
}

.tb_mc_content,
.tb_mc_rating_content {
    margin-bottom: 10px;
    word-break: break-word;
    white-space: pre-line;
}

.tb_mc_content {
    text-align: left;
}

.tb_mc_content_wrap_fixed .tb_mc_rating__ {
    height: 70px;
}

.tb_mc_content_wrap_fixed .tb_mc_rating_content {
    height: 170px;
    overflow-y: auto;
    padding-right: 10px;
}

.tb_mc_content_wrap_fixed .tb_mc_rating_content::-webkit-scrollbar {
    border-radius: 0;
    position: relative;
    right: -5px;
    width: 3px;
}

.tb_mc_content_wrap_fixed .tb_mc_rating_content::-webkit-scrollbar-thumb {
    background-color: rgba(79, 76, 76, 0.35);
    border-radius: 0;
}

.tb_mc_content .tb_text_links_,
.tb_mc_rating_content .tb_text_links_ {
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_mc_rating_content,
.tb_mc_text_post {
    text-align: center;
}

.tb_mc_text_post {
    margin-bottom: 10px;
}

.tb_mc_text_decoration::before {
    content: "\e909";
    font-family: 'WebSocialFonts';
    margin-right: 12px;
}

.tb_mc_text_decoration::after {
    content: "\e90a";
    font-family: 'WebSocialFonts';
    margin-left: 12px;
}

.tb_mc_post_in:hover .tb_share_wrapper {
    opacity: 1;
    visibility: visible;
}

.tb_mc_media_wrap {
    position: relative;
    display: block;
}

.tb_mc_video_ico,
.tb_mc_audio_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 1;
    color: #fff;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));
}

.tb_mc_video_ico {
    font-size: 12px;
}

.tb_mc_audio_ico {
    font-size: 16px;
}

.tb_mc_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));
}

.tb_mc_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));
}

.tb_mc_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_mc_social_actions_ {
    display: flex;
    background-color: rgba(0, 0, 0, .04);
    align-items: center;
    justify-content: end;
    flex-wrap: wrap;
}


.tb_mc_social_action_bg {
    background-color: rgba(0, 0, 0, 0.05);
}

.tb_mc_social_action_bg .tb_social_action__ {
    border-top: none;
}


.tb_mc_action_counts_wrap {
    display: flex;
    padding: 3px;
    margin: 3px;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
}

.tb_mc_action_counts {
    font-size: 12px;
    margin-left: 5px;
    font-weight: 600;
}

.tb_mc_action_ico {
    font-size: 14px;
}

.tb_mc_action_counts,
.tb_mc_action_ico {
    color: #fff;
    transition: all .3s ease-in-out;
}

.tb_mc_action_counts_wrap:hover .tb_mc_action_ico {
    transform: rotateY(180deg);
}

.tb_mc_post_cta {
    display: block;
    text-align: center;
    margin-bottom: 10px;
}

.tb_mc_post_cta_btn {
    font-family: var(--tb-font);
    padding: var(--tb-cta-padding);
    color: var(--tb-cta-color);
    background-color:  var(--tb-cta-bg-color);
    border-radius: var(--tb-cta-border-radius);
    font-weight: var(--tb-cta-font-weight);
    border-color: var(--tb-cta-border-color);
    transition: var(--tb-cta-transition);
    min-height: var(--tb-cta-height);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: normal;
}

.tb_mc_post_cta_btn:hover {
    color: var(--tb-cta-color-hover);
    background-color: var(--tb-cta-bg-color-hover);
    border-color: var(--tb-cta-border-color-hover);
}


.tb_mc_social_top_ {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 15px;
}

.tb_mc_rating__ .tb__rating__{
    padding-top: 10px;
}